import Cookie from "js-cookie";
import { Base64 } from "js-base64";

import { ValidationObserver, ValidationProvider } from "vee-validate";

export default {
  middleware: ["session-control", "admin"],
  layout: "admin",
  components: {
    ValidationObserver,
    ValidationProvider,
  },
  data() {
    return {
      counter: 0,
      inputs: [
        [
          { id: "criteria0", label: "Ölçüt Değeri Adı", value: "" },
          {
            id: "criteriaValue0",
            label: "Ölçüt Değeri",
            value: "",
          },
          {
            id: "criteriaType0",
            label: "Ölçüt Değeri Türü",
            value: "",
          },
        ],
      ],
      e1: 1,
      inputData: {
        name: null,
        description: null,
        id: null,
      },
      userData:
        Cookie.get("userData") !== null &&
        Cookie.get("userData") !== undefined &&
        Cookie.get("userData") !== ""
          ? JSON.parse(Base64.decode(Cookie.get("userData")))
          : null,
    };
  },
  computed: {
    img_url() {
      return process.env.apiPublicUrl;
    },
  },
  methods: {
    cloneProperty() {
      this.inputs.push([
        {
          id: `criteria${++this.counter}`,
          label: "Ölçüt Değeri Adı",
          value: "",
        },
        {
          id: `criteriaValue${++this.counter}`,
          label: "Ölçüt Değeri",
          value: "",
        },
        {
          id: `criteriaType${++this.counter}`,
          label: "Ölçüt Değeri Türü",
          value: "",
        },
      ]);
    },
    removeProperty(id) {
      for (let i = 0; i < this.inputs.length; i++) {
        if (this.inputs[i][0].id === id) {
          this.inputs.splice(i, 1);
        }
      }
    },
    saveCriterias() {
      let formData = new FormData(this.$refs.criteriasForm);

      this.$axios
        .post(process.env.apiBaseUrl + "panel/criteria/create", formData, {
          json: true,
          withCredentials: false,
          mode: "no-cors",
          headers: {
            "Access-Control-Allow-Origin": "*",
            "Access-Control-Allow-Headers":
              "Origin, Content-Type, X-Auth-Token, Authorization",
            "Access-Control-Allow-Methods":
              "GET, POST, PATCH, PUT, DELETE, OPTIONS",
            "Access-Control-Allow-Credentials": true,
            "Content-Type":
              "multipart/form-data; boundary=" + formData._boundary,
            Authorization: "Bearer " + this.userData.api_token,
          },
          credentials: "same-origin",
        })
        .then((response) => {
          if (response.data.success) {
            this.$izitoast.success({
              title: response.data.title,
              message: response.data.msg,
              position: "topCenter",
            });
            this.$refs.myDropzone.options.url =
              process.env.apiBaseUrl +
              "panel/criteria/create-file/" +
              response.data.data.$oid;
            this.$refs.myDropzone.dropzone.options.url =
              process.env.apiBaseUrl +
              "panel/criteria/create-file/" +
              response.data.data.$oid;
            this.options.url =
              process.env.apiBaseUrl +
              "panel/criteria/create-file/" +
              response.data.data.$oid;
            this.inputData.id = response.data.data.$oid;
            this.options.params.title = response.data.name;
            this.e1 = 2;
          } else {
            this.$izitoast.error({
              title: response.data.title,
              message: response.data.msg,
              position: "topCenter",
            });
          }
        });
    },
  },
};
